/*
body {
  font-family: 'Inter', sans-serif; 
}
*/

/* Custom scrollbar (optional, example for webkit browsers) */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

/* Dark mode scrollbar (optional)*/
@media (prefers-color-scheme: dark) {
    ::-webkit-scrollbar-track {
      background: #333;
    }

    ::-webkit-scrollbar-thumb {
      background: #bbb;
    }

    ::-webkit-scrollbar-thumb:hover {
      background: #ddd;
    }
}